// Making img tag src image to parent's background image
//
function ImgToBg(parentClass, imgClass) {
	$('.' + parentClass)
		.css({
			'background-image':
				'url(' +
				$('.' + parentClass)
					.find('img.' + imgClass)
					.attr('src') +
				')',
		})
		.find('img.' + imgClass)
		.remove();
}
